<template>
  <li>
    <div class="request-block">
      <div
        class="request-block__info"
        v-bind:class="{ denied: reqItem.isDenied }"
      >
        <div class="info__header">
          <div class="date">{{ reqItem.created }}</div>
          <h2 class="title">{{ reqItem.title }}</h2>
          <div class="copy">
            <button class="copy-button" type="button">
              <CopyButton @copy="copyRequest" />
            </button>
          </div>
        </div>
        <h2 class="quantity">{{ reqItem.quantity }}</h2>
        <p>При текущем расходе запаса хватит на {{ reqItem.leftover }} мес.</p>
      </div>
      <div class="request-block__buttons">
        <button
          class="requested-button"
          v-if="!reqItem.isDenied"
          v-on:click="$emit('put-in-work', reqItem.id)"
        >
          Заказано
        </button>
        <button
          class="denied-button"
          v-on:click="reqItem.isDenied = !reqItem.isDenied"
        >
          {{ reqItem.isDenied ? "Принять" : "Отклонить" }}
        </button>
      </div>
    </div>
  </li>
</template>

<script>
import CopyButton from "@/components/CopyButton";
export default {
  props: {
    reqItem: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      inWork: this.reqItem.inWork,
    };
  },
  components: {
    CopyButton,
  },
  methods: {
    isInWorkNow() {
      this.$emit("remove", this.id);
    },
    copyRequest() {
      const stringToCopy = `Прошу изготовить ${this.reqItem.title} в количестве ${this.reqItem.quantity} штук`;
      var el = document.createElement("textarea");
      el.value = stringToCopy;
      el.setAttribute("readonly", "");
      el.style.position = "absolute";
      el.style.left = "-9999px";
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
    },
  },
};
</script>

<style scoped>
li {
  margin-bottom: 25px;
}
.request-block {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  max-width: 90%;
}
.request-block__info {
  display: flex;
  flex-direction: column;
}
.info__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.title {
  transform: translateX(-10%);
}
.requested-button,
.denied-button {
  position: relative;
  margin-bottom: -2px;
  bottom: 0;
  width: 50%;
  height: 30px;
  border: 1px solid black;
  font-size: 1.2rem;
}
.requested-button {
  background: rgba(17, 189, 31, 0.096);
}
.requested-button:hover {
  background: rgba(17, 189, 31, 0.5);
}
.denied-button {
  background: rgba(134, 54, 54, 0.144);
}
.denied-button:hover {
  background: rgba(129, 21, 21, 0.5);
}
.copy-button {
  background-color: transparent;
  width: 50px;
  border: none;
}
.copy-button:hover {
  opacity: 0.5;
}
button:hover {
  cursor: pointer;
}
.denied {
  opacity: 0.5;
  background: #ccc;
}
</style>
