<template>
  <div class="orders-block">
    <h2>В работе {{ actives }} {{ pickWord(actives) }}</h2>
    <ul>
      <OrderItem v-for="(ord, i) in inWork" :key="i" :orderItem="ord" 
        v-on:remove="removeItem"
        @return-to-requests="returnToRequests"
      />
    </ul>
  </div>
</template>

<script>
import OrderItem from '@/components/OrderItem'
export default {
  props: ["orders"],
  components: {
    OrderItem,
  },
  computed: {
    actives() {
      return this.orders.filter((el) => el.inWork).filter((el) => el.active).length || 0
    },
    inWork() {
      return this.orders.filter((el) => el.inWork)
    }
  },
  methods: {
    returnToRequests(id) {
      this.$emit('return-to-requests', id)
    },
    removeItem(id) {
      this.$emit('remove', id)
    },
    pickWord(count) {
      let rem = count % 100;
      if (rem < 11 || rem > 14) {
        rem = rem % 10;
        if (rem === 1) return "заказ";
        if (rem >= 2 && rem <= 4) return "заказа";
      }
      return "заказов";
    },
  },
};
</script>

<style scoped>
ul {
  list-style: none;
}
</style>
