<template>
  <div id="app">
    <Header @add-request="newRequest" />
    <div class="container">
      <RequestsList :request="requestsArr" v-on:put-in-work="putInWork" />
      <OrderList
        :orders="requestsArr"
        v-on:remove="removeItem"
        @return-to-requests="returnToRequests"
      />
    </div>
  </div>
</template>

<script>
import { db } from "./main";
import Header from "@/components/Header";
import RequestsList from "@/components/RequestsList";
import OrderList from "@/components/OrderList";
export default {
  name: "App",
  data() {
    return {
      requestsArr: [],
    };
  },
  firestore() {
    return {
      requestsArr: db.collection("requestsArr"),
    };
  },
  components: {
    Header,
    RequestsList,
    OrderList,
  },
  methods: {
    returnToRequests(id) {
      db.collection("requestsArr")
        .doc(id)
        .update({ inWork: false });
    },
    putInWork(id) {
      db.collection("requestsArr")
        .doc(id)
        .update({ inWork: true });
    },
    newRequest(elem) {
      db.collection("requestsArr").add(elem);
    },
    removeItem(id) {
      this.requestsArr.filter((el) => {
        if (el.id === id) {
          db.collection("requestsArr")
            .doc(id)
            .delete();
        }
      });
    },
  },
};
</script>

