<template>
  <div class="header">
    <h3>Создание новой заявки</h3>
    <form v-on:submit.prevent="onSubmit">
      <input
        v-model="materialTitle"
        placeholder="Материал"
        id="inpTitle"
      />
      <input
        v-model.number="materialNumber"
        placeholder="Количество"
        id="inpQuantity"
      />
      <input
        v-model.number="materialLeftover"
        placeholder="Запас в месяцах"
        id="inpLeftover"
      />
      <button  type="submit">Создать</button>
    </form>
    <hr />
  </div>
</template>

<script>
export default {
  props: ["request"],
  data() {
    return {
      materialTitle: "",
      materialNumber: "",
      materialLeftover: "",
      date: Date.now(),
    };
  },
  methods: {
    onSubmit() {
      const newRequest = {
        // id: Date.now(),
        title: this.materialTitle,
        quantity: this.materialNumber,
        leftover: this.materialLeftover,
        active: true,
        inWork: false,
        isDenied: false,
        created: new Date(this.date).toLocaleDateString(),
      };
      this.$emit('add-request', newRequest);
      this.materialTitle = '';
      this.materialNumber = '';
      this.materialLeftover = '';
    },
  },
  computed: {
    today:()=> {
      return new Date(this.date).toLocaleDateString();
    },
  },
};
</script>

<style scoped>
hr {
  margin: 20px 0;
}
</style>
