<template>
  <div class="requests-block">
    <h2>Потребность склада: {{ actives }} {{ pickWord(actives) }}</h2>
    <ul>
      <RequestItem
        v-for="(req, i) in notInWork"
        :key="i"
        :reqItem="req"
        v-on:put-in-work="putInWork"
      />
    </ul>
  </div>
</template>

<script>
import RequestItem from "@/components/RequestItem";
export default {
  props: ["request"],
  components: {
    RequestItem,
  },
  computed: {
    actives() {
      return this.request
        .filter((el) => !el.inWork)
        .filter((el) => !el.isDenied).length;
    },
    notInWork() {
      return this.request.filter((el) => !el.inWork);
    },
  },
  methods: {
    putInWork(id) {
      this.$emit("put-in-work", id);
    },
    pickWord(count) {
      let rem = count % 100;
      if (rem < 11 || rem > 14) {
        rem = rem % 10;
        if (rem === 1) return "материал";
        if (rem >= 2 && rem <= 4) return "материала";
      }
      return "материалов";
    },
  },
};
</script>

<style scoped>
ul {
  list-style: none;
}
</style>
