<template>
  <li>
    <div class="order-block">
      <div class="order-block__info">
        <div class="info__header">
          <div class="date">{{ orderItem.created }}</div>
          <h2 class="title">{{ orderItem.title }}</h2>
          <div class="copy">
            <button class="copy-button" type="button">
              <CopyButton @copy="copyRequest" />
            </button>
          </div>
        </div>
        <h2 class="quantity">{{ orderItem.quantity }}</h2>
        <p>
          При текущем расходе запаса хватит на {{ orderItem.leftover }} мес.
        </p>
      </div>
      <div class="order-block__buttons">
        <button
          class="return-button"
          @click="$emit('return-to-requests', orderItem.id)"
        >
          Вернуть в запросы
        </button>
        <button
          class="accepted-button"
          v-on:click="$emit('remove', orderItem.id)"
        >
          Поступило на склад
        </button>
      </div>
    </div>
  </li>
</template>

<script>
import CopyButton from "@/components/CopyButton";
export default {
  components: {
    CopyButton,
  },
  props: {
    orderItem: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      inWork: this.orderItem.inWork,
    };
  },
  methods: {
    copyRequest() {
      const stringToCopy = `Прошу изготовить ${this.orderItem.title} в количестве ${this.orderItem.quantity} штук`;
      var el = document.createElement("textarea");
      el.value = stringToCopy;
      el.setAttribute("readonly", "");
      el.style.position = "absolute";
      el.style.left = "-9999px";
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
    },
  },
};
</script>

<style scoped>
li {
  margin-bottom: 25px;
}
.order-block {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  max-width: 90%;
}
.order-block__info {
  display: flex;
  flex-direction: column;
}
.info__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.title {
  transform: translateX(-10%);
}
.return-button {
  position: relative;
  margin-bottom: -2px;
  bottom: 0;
  width: 50%;
  height: 30px;
  border: 1px solid black;
  font-size: 1.2rem;
  background: rgba(129, 21, 21, 0.5);
}
.accepted-button {
  position: relative;
  margin-bottom: -2px;
  bottom: 0;
  width: 50%;
  height: 30px;
  border: 1px solid black;
  font-size: 1.2rem;
  background: rgba(69, 84, 218, 0.5);
}
.accepted-button:hover {
  background: rgb(69, 84, 218);
}

.copy-button {
  background-color: transparent;
  width: 50px;
  border: none;
}
.copy-button:hover {
  opacity: 0.5;
}
button:hover {
  cursor: pointer;
}
</style>
